/*== Grid ==*/
/* Grid clearfix */
.ba-row:before, .ba-row:after {
  content: " ";
  display: table;
}

.ba-row:after {
  clear: both;
}

.ba-row + .ba-row {
  margin-top: 20px;
}

[class*="ba-width-"] {
  width: 100%;
}

.ba-row > [class*="ba-width-"] {
  float: left;
  padding-left: 20px;
}

.ba-row {
  margin-left: -20px;
}

/* No Padding Grid */
.ba-row-nopad {
  margin-left: 0;
}

.ba-row-nopad + .ba-row-nopad {
  margin-top: 0;
}

.ba-row-nopad > [class*="ba-width-"] {
  padding-left: 0;
}

.ba-width-1-2, .ba-width-2-4, .ba-width-3-6 {
  width: 50%;
}

.ba-width-1-3, .ba-width-2-6 {
  width: 33.333%;
}

.ba-width-2-3, .ba-width-4-6 {
  width: 66.667%;
