/* Phone landscape and bigger */
@media (min-width: 480px) {
  .ba-float-left-small {
    float: left;
  }
  .ba-float-right-small {
    float: right;
  }
  /* Whole */
  .ba-width-small-1-1 {
    width: 100%;
  }
  /* Halves */
  .ba-width-small-1-2, .ba-width-small-2-4, .ba-width-small-3-6 {
    width: 50%;
  }
  /* Thirds */
  .ba-width-small-1-3, .ba-width-small-2-6 {
    width: 33.333%;
  }
  .ba-width-small-2-3, .ba-width-small-4-6 {
    width: 66.666%;
  }
  /* Quarters */
  .ba-width-small-1-4 {
    width: 25%;
  }
  .ba-width-small-3-4 {
    width: 75%;
  }
  /* Fifths */
  .ba-width-small-1-5 {
    width: 20%;
  }
  .ba-width-small-2-5 {
    width: 40%;
  }
  .ba-width-small-3-5 {
    width: 60%;
  }
  .ba-width-small-4-5 {
    width: 80%;
  }
}

